import React from 'react';

class Header extends React.Component {
    state = {
        isIntro: true,
        isPortfolio: false,
        isResume: false,
        isContact: false,
        intro: 0,
        portfolio: 0,
        contact: 0
    };

    setAllStates(isIntro, isPortfolio, isResume, isContact) {
        this.setState({ isIntro, isPortfolio, isResume, isContact });
    }
    
    setAllSectionPosition() {
        const     intro = document.getElementById("intro").offsetTop;
        const portfolio = document.getElementById("portfolio").offsetTop;
        const    resume = document.getElementById("resume").offsetTop;
        const   contact = document.getElementById("contact").offsetTop;
        this.setState({ intro, portfolio, resume, contact });
    }

    componentDidMount() {
        this.setAllSectionPosition();

        document.addEventListener('scroll', () => {
            const currentPosition = window.scrollY;

            if(currentPosition < this.state.portfolio) {
                this.setAllStates(true, false, false, false);
            } else if(currentPosition < this.state.resume) {
                this.setAllStates(false, true, false, false);
            } else if(currentPosition < this.state.contact) {
                this.setAllStates(false, false, true, false);
            } else {
                this.setAllStates(false, false, false, true);
            }
        });
    }
    render() {
        return (
            <header id="header" className="d-flex flex-column justify-content-center">
                <nav className="nav-menu">
                    <ul>
                        <li className={this.state.isIntro ? 'active' : ''}><a href="#intro"><i className="bx bx-home"></i> <span>Home</span></a></li>
                        <li className={this.state.isPortfolio ? 'active' : ''}><a href="#portfolio"><i className="bx bx-book-content"></i> <span>Portfolio</span></a></li>
                        <li className={this.state.isResume ? 'active' : ''}><a href="#resume"><i className="bx bx-file-blank"></i> <span>Resume</span></a></li>
                        <li className={this.state.isContact ? 'active' : ''}><a href="#contact"><i className="bx bx-envelope"></i> <span>Contact</span></a></li>
                    </ul>
                </nav>
            </header>
        );
    }
}

export default Header;